<style scoped>
.info {
  width: 100%;
}
.infocenter {
  padding: 24px 40px;
}
.infocenter > p {
  padding: 12px;
  display: flex;
}
.infocenter > p > span:nth-child(1) {
  padding: 12px 24px;
  background: #f3f5f7;
  margin-right: 50px;
}
.infocenter > p > span:nth-child(2) {
  flex: 1;
  padding: 12px 24px;
  border-bottom: 1px solid #d9dde1;
}
</style>
<template>
  <div>
    <div class="info">
      <h2>
        订单详情
      </h2>
      <div class="infocenter">
        <p v-show="msg == ''" v-for="(item, index) in datas" :key="index">
          <span>订单号:{{ item.id }}</span
          ><span
            >订单时间:{{ item.buy_time | datatime }}&nbsp;花费:{{
              item.buy_money
            }}&nbsp;
          </span>
        </p>
        <p v-if="msg!=''">{{ msg }}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      datas: [],
      msg: "",
      sj: 1578575367504
    };
  },
  methods: {
    orderinfo() {
      this.$axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/toorderinfor",
        params: {
          uid: JSON.parse(window.sessionStorage.getItem("user")).id
        }
      }).then(res => {
        // window.console.log(res.data);
        if (res.data.data.star == 200) {
          this.datas = res.data.data.data;
        } else {
          this.msg = res.data.data.msg;
        }
      });
    }
  },
  mounted() {
    this.orderinfo();
  },
  filters: {
    datatime(val) {//时间戳转时间
      var timestamp4 = new Date(val);
      return (
        timestamp4.toLocaleDateString().replace(/\//g, "-") +
        " " +
        timestamp4.toTimeString().substr(0, 8)
      );
    }
  }
};
</script>
